@keyframes shadow-inset-center {
  0% {
    box-shadow: inset 0 0 0 0 transparent;
  }
  to {
    box-shadow: inset 0 0 14px 0 rgb(0 0 0/50%);
  }
}
.div7 {
  top: 87px;
  left: 58px;
  display: inline-block;
  width: 64px;
}
.div7,
.icon2,
.inner {
  position: absolute;
}
.inner {
  top: 0;
  left: 0;
  border-radius: 50%;
  background-color: var(--color-lightskyblue);
  width: 64px;
  height: 64px;
}
.icon2 {
  height: 38.66%;
  width: 33.48%;
  top: 29.78%;
  right: 27.46%;
  bottom: 31.56%;
  left: 39.06%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.button3,
.ellipse-div {
  position: absolute;
  width: 64px;
  height: 64px;
}
.button3 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  top: 17px;
  left: 55px;
}
.button3:hover,
.button4:hover {
  animation: 1s ease 0s infinite normal none shadow-inset-center;
  opacity: 1;
}
.ellipse-div {
  top: 0;
  left: 0;
  border-radius: 50%;
  background-color: var(--color-salmon);
}
.icon3 {
  position: absolute;
  height: 40.63%;
  width: 40.63%;
  top: 29.69%;
  right: 29.69%;
  bottom: 29.69%;
  left: 29.69%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.button4,
.div8 {
  position: absolute;
  left: 56px;
  width: 64px;
}
.button4 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  top: 114px;
  height: 64px;
}
.div8 {
  top: 185px;
  display: inline-block;
}
.header1 {
  position: relative;
  background-color: var(--color-black);
  width: 100%;
  height: 215px;
  overflow: hidden;
  cursor: pointer;
  text-align: left;
  font-size: var(--font-size-mini);
  color: var(--color-white);
  font-family: var(--font-inter);
}
